<template>
  <el-badge
    v-for="(item, index) in data"
    :key="index"
    :value="item.number"
    class="tag"
  >
    <el-button size="small" @click="route_to_article_list(item.name)">{{
      item.name
    }}</el-button>
  </el-badge>
</template>
<script>
import axios from "axios";
export default {
  // 根据page_now去跟新页面
  // 几乎可以全部复用，到时候再改
  data() {
    return {
      data: [],
    };
  },
  methods: {
    get_page_data() {
      const url = this.$store.state.base_url + `/tags/tags_list.json`;
      axios.get(url).then((response) => {
        console.log(response.data);
        this.data = response.data;
      });
    },
    route_to_article_list(name) {
      this.$router.push({ name: "tag_index", params: { tag_name: name } });
    },
  },
  mounted() {
    document.title = this.$store.state.base_title + "标签分类";
    this.get_page_data();
  },
};
</script>
<style lang="scss" scoped>
.tag {
  margin-top: 20px;
  margin-left: 20px;
}
</style>